}

.flex-container .btn-success { 
  padding-bottom: 9px;
  padding-top: 4px;
}

.flex-container .btn-danger {
  margin-right: 9px;
  margin-left: 9px;
  padding-bottom: 9px;
  padding-top: 4px;
} 


